import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function revenueSurprisePct(tableName: string) {
  const title = "% Rev. Surprise"
  return genericColumn({
    endpoint: "companyLatestSurprise",
    columnKeys: ["revenueSurprisePct"],
    size: 120,
    title,
    enableSorting: true,
    enableHiding: true,
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip="Explain the % Revenue Surprise"
          queryResult={info.row.original.financialData.companyLatestSurprise}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `Who has recently released Revenue earnings surprise for ${info.row.original.entityInfo.name}?`,
              value:
                info.row.original.financialData.companyLatestSurprise?.data?.revenueSurprisePct,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyLatestSurprise?.data?.revenueSurprisePct}
        />
      )
    },
  })
}

export { revenueSurprisePct }
